@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

.profile {
  background: linear-gradient(180deg, #f8fafc 0%, #f1f5f9 100%);
  font-family: 'Inter', sans-serif;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding: 40px 20px;
  width: 100%;

  &__container {
    width: 100%;
    max-width: 680px;
    margin: 0 auto;
  }

  &__title {
    font-size: 36px;
    font-weight: 700;
    color: #1f2937;
    text-align: center;
    margin-bottom: 40px;
    letter-spacing: -0.02em;
  }

  &__article {
    background: #ffffff;
    padding: 32px;
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
    display: flex;
    flex-direction: column;
    gap: 32px;
    transition: transform 0.3s ease, box-shadow 0.3s ease;

    &:hover {
      transform: translateY(-4px);
      box-shadow: 0 12px 32px rgba(0, 0, 0, 0.12);
    }
  }

  &__header {
    display: flex;
    align-items: center;
    gap: 20px;
    padding-bottom: 24px;
    border-bottom: 1px solid #e5e7eb;
  }

  &__avatar {
    width: 80px;
    height: 80px;
    background: #2563eb;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    font-weight: 600;
    color: #ffffff;
    text-transform: uppercase;
    flex-shrink: 0;
  }

  &__header_info {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__name {
    font-size: 28px;
    font-weight: 600;
    color: #1f2937;
    margin: 0;
    line-height: 1.2;
  }

  &__role {
    font-size: 18px;
    font-weight: 500;
    color: #6b7280;
    margin: 0;
  }

  &__subtitle {
    font-size: 20px;
    font-weight: 600;
    color: #1f2937;
    margin: 0;
    letter-spacing: -0.01em;
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__field {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 0;
    border-bottom: 1px solid #f1f5f9;
    transition: background 0.3s ease;

    &:hover {
      background: #f8fafc;
      border-radius: 6px;
    }
  }

  &__label {
    font-size: 15px;
    font-weight: 500;
    color: #4b5563;
  }

  &__value {
    font-size: 15px;
    font-weight: 500;
    color: #1f2937;
    text-align: right;
  }

  &__notification {
    padding: 12px 20px;
    font-size: 14px;
    font-weight: 500;
    color: #ffffff;
    border-radius: 8px;
    text-align: center;
    margin-top: 24px;
    animation: fadeIn 0.3s ease-in-out;

    &--success {
      background: #16a34a;
    }

    &--error {
      background: #dc2626;
    }
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @media (max-width: 768px) {
    padding: 32px 16px;

    &__container {
      max-width: 100%;
    }

    &__title {
      font-size: 32px;
      margin-bottom: 32px;
    }

    &__article {
      padding: 24px;
    }

    &__avatar {
      width: 64px;
      height: 64px;
      font-size: 28px;
    }

    &__name {
      font-size: 24px;
    }

    &__role {
      font-size: 16px;
    }

    &__subtitle {
      font-size: 18px;
    }

    &__field,
    &__label,
    &__value {
      font-size: 14px;
    }

    &__notification {
      font-size: 13px;
      padding: 10px 16px;
    }
  }

  @media (max-width: 480px) {
    padding: 24px 12px;

    &__title {
      font-size: 28px;
      margin-bottom: 24px;
    }

    &__article {
      padding: 16px;
    }

    &__avatar {
      width: 56px;
      height: 56px;
      font-size: 24px;
    }

    &__name {
      font-size: 20px;
    }

    &__role {
      font-size: 14px;
    }

    &__subtitle {
      font-size: 16px;
    }

    &__field,
    &__label,
    &__value {
      font-size: 13px;
    }

    &__notification {
      font-size: 12px;
      padding: 8px 12px;
    }
  }
}